
    import { defineComponent, onMounted, computed, watch, ref, onBeforeUnmount} from "vue";

    export default defineComponent({
        name: 'AffectiveText',
        emits: ['onAnimationEnd'],
        props: {
            id: String,
            style: String,
            message: String,
            charInterval: {
                type: Number,
                required: true
            },
            delay: {
                type: Number,
                required: true,
                default: 0
            },
            userInitials: {
                type: String,
                required: false,
                default: 'CS'
            },
            userName: {
                type: String,
                required: false,
                default: 'C. Schmidt'
            },
            userShort: {
                type: String,
                required: false,
                default: '@cschmidt'
            }
        },

        setup(props, context) {
            // variables
            let timer = -1;
            let isPlaying = ref(false);
            let isWriting = ref(false);
            let charIndex = ref(0);
            let playCount = ref(0);
            let tBuffer = 0;
            let tRedraw = 0;
            let tAnimation = ref(0);
            let redrawAnimation: any;
            let currentString = computed(() => {
                if (props.message != undefined && charIndex.value >= 0 ){
                    return props.message.substring(0, charIndex.value);
                }
                return '';
            });
            let iMessageInput = ref('');
            let iMessageSentMsg = ref('');

            // hooks n watcher
            onMounted(() => {
                console.log("mounted");
            })
            onBeforeUnmount(() => {
                stop();
            })

            // methods
            function play() {
                if (!isPlaying.value) {
                    charIndex.value = 0;
                    isPlaying.value = true;
                    if (props.message != undefined ) {

                        // timer
                        // timer = setInterval(() => {
                        //     if (charIndex.value >= props.message.length) {
                        //         stop();
                        //         playCount.value++;
                        //         context.emit('onAnimationEnd', {
                        //             id: props.id,
                        //             value: playCount.value
                        //         });
                        //     } else {
                        //         charIndex.value++;
                        //     }
                        // }, props.charInterval);

                        // animationFrame
                        tAnimation.value = 0;
                        tRedraw = 0;
                        tBuffer = 0;
                        isWriting.value = false;
                        redrawAnimation = animationLoop();
                    }
                }
            }

            function stop() {
                isPlaying.value = false;
                if (timer) {
                    clearInterval(timer);
                }
                if (redrawAnimation) {
                    cancelAnimationFrame(redrawAnimation);
                }
            }

            function send() {
                iMessageSentMsg.value = iMessageInput.value;
                iMessageInput.value = '';
                play();
            }

            function animationLoop() {
                if(isPlaying.value) {
                    // get current date, get delta to last frame
                    const t = Date.now();
                    if (tBuffer <= 0) {
                        tBuffer = t;
                        tRedraw = 0;
                    }
                    const dt = t - tBuffer;

                    // update at specified rate
                    tBuffer = t;
                    tRedraw += dt;
                    if (tRedraw >= 30) {

                        // wait until delay is over (or undefined)
                        if (tAnimation.value <= props.delay) {
                            isWriting.value = true;
                        }


                        if (tAnimation.value >= props.delay) {
                            isWriting.value = false;
                            // inc charIndex at charInterval
                            const p = Math.round((tAnimation.value - props.delay) / props.charInterval);
                            if (p != charIndex.value) {
                                charIndex.value = p;
                                //console.log('redraw ' + p);
                            }

                            // stop
                            if (props.message == undefined || charIndex.value >= props.message.length) {
                                stop();
                                playCount.value++;
                                context.emit('onAnimationEnd', {
                                    id: props.id,
                                    value: playCount.value
                                });
                            }
                        }

                        // reset redraw buffer
                        tAnimation.value += tRedraw;
                        tRedraw = 0;
                    }

                    // next frame
                    requestAnimationFrame(animationLoop);
                }
            }

            // return data
            return {
                charIndex,
                currentString,
                timer,
                isPlaying,
                playCount,
                tRedraw,
                tBuffer,
                tAnimation,
                redrawAnimation,
                isWriting,
                iMessageInput,
                iMessageSentMsg,

                play,
                stop,
                animationLoop,
                send
            }
        }
    })
