
    import {  defineComponent, onMounted, computed, watch, ref, onBeforeUnmount } from "vue";


    export default defineComponent({
        name: 'SpeechViz',
        emits: ['onAnimationEnd'],
        props: {
            id: String,
            sentenceAnalysis: Array
    },


    setup(props, context) {
        let canvasWidth = 400;
        let canvasHeight = 80;
        let title = "Speech Visualization";
        let aniProgress = 0;
        const aniRunning = ref(false);
        let aniDur = 1500;
        let aniLoop = 1;
        let aniCount = 0;
        const playCount = ref(0)
        let aniVariant = "stretch";
        let text  = "This is totally not what I expected";
        let redrawRunning = false;
        let tRedraw = 0;
        let redrawAnimation: any;
        let tBuffer = 0;
        let canvas: HTMLCanvasElement|undefined;
        let dpr = 1;
        const sentenceDefinition:{text:string,del:number,pos:number, dur?:number}[] = [
            {text: 'This ', del: 0, pos: 26},
            {text: 'is ', del: 0, pos: 60},
            {text: 'totally ', del: 0, pos: 105},
            {text: 'not ', del: 0, pos: 162},
            {text: 'what ', del: 0, pos: 217},
            {text: 'I ', del: 0, pos: 247},
            {text: 'expected', del: 0, pos: 302},
        ]

        const sentence = computed(() => {            
            const result = [ ...sentenceDefinition ]
            console.log("------------------")
            
            if(result.length === props.sentenceAnalysis!.length){
                for(let i = 0; i < result.length; i++){
                    if(i>0){
                        result[i].dur = (props.sentenceAnalysis![i] as any).time - (props.sentenceAnalysis![i-1] as any).time ; 
                    }
                    else {
                        result[i].dur = (props.sentenceAnalysis![i] as any).time; 
                    }
                }
            }
            console.log(result)
            return result;
        })

        //functions
        onMounted(() => {
            canvas = document.getElementById('mainCanvas') as HTMLCanvasElement;
            const ctx = canvas.getContext('2d')!;
            console.log("onMounted: "+props.sentenceAnalysis)
            // f*ck around to get crisp text on the canvas
            dpr = window.devicePixelRatio;
            canvas.width = Math.floor(canvasWidth * dpr);
            canvas.height = Math.floor(canvasHeight * dpr);
            ctx.scale(dpr, dpr);

            // start redraw loop
            startRedraw();
        })

        function startRedraw(): void {
            redrawRunning = true;
            tBuffer = -1;
            redrawLoop();
        }

        function startAni(): void {
            aniProgress = 0;
            text = getTextFromSentence(sentence.value);
            aniDur = calcAniDuration(sentence.value);
            aniRunning.value = true;
            playCount.value++;
            context.emit('onAnimationEnd', {
                id: props.id,
                value: playCount.value
            });
        }

        function stopAni(): void {
            aniRunning.value = false;
        }


        function pressPlay():void {
            context.emit('onAnimationEnd', {
                id: props.id,
                value: playCount.value
            });
            playAni('stretch');
        }


        function playAni(variant:string): void{
            stopAni();
            aniVariant = variant;
            startAni();
        }

        function redrawLoop(): void {
            if (redrawRunning) {
                // calc time to last call and time to redraw
                const t = Date.now();
                if (tBuffer < 0) {
                    tBuffer = t;
                    tRedraw = 0;
                }
                const dt = t - tBuffer;
                tBuffer = t;
                tRedraw += dt;

                // redraw every x ms
                if (tRedraw >= 33) {

                    if(canvas) {
                        const ctx = canvas.getContext('2d')!;
                        const w = canvasWidth;
                        const h = canvasHeight;
                        canvas.width = Math.floor(w * dpr);
                        canvas.height = Math.floor(h * dpr);
                        ctx.scale(dpr, dpr);

                        ctx.save();
                        ctx.clearRect(0, 0, w, h);
                        // ctx.fillStyle = '#000000';
                        // ctx.fillRect(0, 0, w, h);
                        // ctx.closePath();

                        // bg
                        const rx = 0;
                        const ry = 0;
                        const rr = 6;
                        const rw = canvasWidth;
                        const rh = canvasHeight;
                        ctx.beginPath();
                        ctx.moveTo(rx + rr, ry);
                        ctx.arcTo(rx + rw, ry, rx + rw, ry + rh, rr);
                        ctx.arcTo(rx + rw, ry + rh, rx, ry + rh, rr);
                        ctx.arcTo(rx, ry + rh, rx, ry, rr);
                        ctx.arcTo(rx, ry, rx + rw, ry, rr);
                        ctx.fillStyle = '#ffffff';
                        ctx.fill();
                        ctx.closePath();

                        // padding
                        const px = 10;
                        const py = 20;

                        // cursor
                        if (aniRunning.value) {
                            const char = calcCharAnimation(aniProgress, aniDur, sentence.value);

                            // strechting cursor
                            if (aniVariant == 'stretch') {
                                const cw = Math.sin(char.dwellProgress * Math.PI) * 26;
                                const ch = 8;
                                const cx = char.pos + px - cw/2;
                                const cy = 46;
                                const cr = 2;
                                ctx.beginPath();
                                ctx.moveTo(cx + cr, cy);
                                ctx.arcTo(cx + cw, cy, cx + cw, cy + ch, cr);
                                ctx.arcTo(cx + cw, cy + ch, cx, cy + ch, cr);
                                ctx.arcTo(cx, cy + ch, cx, cy, cr);
                                ctx.arcTo(cx, cy, cx + cw, cy, cr);
                                ctx.fillStyle = '#ddd';
                                ctx.fill();
                                ctx.closePath();

                            // drawing signal
                            } else if(aniVariant == 'signal') {
                                const cw = 8;
                                const cx = char.pos + px - cw/2;
                                ctx.beginPath();
                                ctx.arc(cx, 46, cw/2, 0, 2 * Math.PI);
                                ctx.fillStyle = '#666';
                                ctx.fill();
                                ctx.closePath();
                            }
                        }

                        // text
                        ctx.font = '24px Arial';
                        ctx.textBaseline = "hanging";
                        ctx.fillStyle = "#222222"
                        ctx.fillText(text, px, py);
                    }

                    // calc animation progress (0..1)
                    aniProgress += (tRedraw / aniDur);
                    if (aniProgress > 1) {
                        aniProgress = 0;
                        aniCount++;
                        if (aniLoop > 0 && aniCount >= aniLoop) {
                            stopAni();
                        }
                    }

                    // reset redraw buffer
                    tRedraw = 0;
                }

                // next frame
                redrawAnimation = requestAnimationFrame(redrawLoop);
            }
        }

        function getTextFromSentence(sentence: any): string {
            var str = ''
            for (var s of sentence) {
                str += s.text;
            }
            str = str.trim();
            return str;
        }

        function calcAniDuration(sentence: any): number {
            var dur = 0;
            for (var s of sentence) {
                dur += s.del;
                dur += s.dur;
            }
            return dur;
        }

        function  calcCharAnimation(totalLerp: number, totalDur: number, sentence: any): any {

            // calculate at what point of time of the animation we are at
            const t = totalLerp * totalDur;

            // get the current string section we are at
            var curSection;
            var tStart = 0;
            for (var s of sentence) {
                tStart += s.del;
                if (t >= tStart) {
                    curSection = s;
                    curSection.t = tStart;
                }
                tStart += s.dur;
            }

            // return (a) the string section's visual position
            // and (b) the remaining dwell time for this section
            var pos = 0;
            var dt = 0;
            var dp = 0;
            if (curSection) {
                dp = Math.min(1, (t - curSection.t) / curSection.dur)
                dt = curSection.dur;
                pos = curSection.pos;
            }
 
            return {pos: pos, dwellTime:dt, dwellProgress: dp};
        }
        return {
            playAni,
            title,
            aniRunning,
            playCount,
            pressPlay
        }
    }
    })
