
import {
  defineComponent,
  onMounted,
  computed,
  watch,
  ref,
  onBeforeUnmount,
} from "vue";

export default defineComponent({
  name: "SpeechToText",
  emits: ["speechend"],
  props: {
      text: String,
      aff: String,
      sentence: String,
      errormsg: String,
    lang: {
      type: String,
      default: "en-US",
    },
  },

  setup(props, context) {
    
    let SpeechRecognition =
      (window as any).SpeechRecognition ||
      (window as any).webkitSpeechRecognition;
    let recognition = SpeechRecognition ? new SpeechRecognition() : false;

    let speakingTime = 0;
    let starttime = 0;

    type SentenceData = {
        sentence: string;
        duration: number;
    }

    type RunTimeData = {
        words: string;
        time: number;
    }
    
    const finalSentence = ref("")

    const error = ref("");
    const speaking = ref(false);
    const toggle = ref(false);
    const runtimeTranscription = ref("");
    const sentences = ref<SentenceData[]>([]);
    const runtimes = ref<RunTimeData[]>([]);


    onMounted(() => {
      console.log("mounted");
      checkCompatibility();


      recognition.addEventListener("audiostart", function () {
        speaking.value = true;
        starttime = Date.now();
        console.log('start '+new Date(Date.now()).toLocaleDateString())
      });

      recognition.addEventListener("audioend", function () {
        speaking.value = false;
        //console.log(Date.now());
        speakingTime = Date.now() - starttime;
        //console.log('end: '+new Date(Date.now()).toLocaleDateString())
        //console.log('delta:'+speakingTime)
      });

      recognition.addEventListener("result", (event: any) => {
        const text = Array.from(event.results)
          .map((result: any) => result[0])
          .map((result) => result.transcript)
          .join("");
        
        runtimes.value.push({words:runtimeTranscription.value, time: Date.now() - starttime});
        runtimeTranscription.value = text;

        //console.log('rt: '+runtimeTranscription.value)
      });

      recognition.addEventListener("end", () => {
        if (runtimeTranscription.value !== "") {
            finalSentence.value = runtimeTranscription.value
          sentences.value.push({sentence:runtimeTranscription.value, 
                                duration:speakingTime});
          sentences.value = [...sentences.value];
        }
        runtimeTranscription.value = "";
        //console.log(runtimes.value);
        //console.log("finally: "+finalSentence.value)
        //endSpeechRecognition();

      });
    });

    onBeforeUnmount(() => {
      recognition.stop();
    });

    // methods

    function checkCompatibility() {
      if (!recognition) {
        error.value =
          "Speech Recognition is not available on this browser. Please use Chrome or Firefox";
      }
    }

    function startSpeechRecognition() {
      toggle.value = true;
      recognition.lang = props.lang;
      recognition.interimResults = true;
      recognition.start();
      console.log("Started speech recognition")
    }

    function endSpeechRecognition() {
      recognition.stop();
      toggle.value = false;
      context.emit("speechend", {
        runtimes: runtimes.value,
        finalSentence: finalSentence.value,
      });
      sentences.value = []
      runtimes.value = []
      finalSentence.value = ""
    }

    return {
      sentences,
      startSpeechRecognition,
      endSpeechRecognition,
      toggle,
      runtimeTranscription,
      error,
    };
  },
});
