            function updateOrCreateFirebaseSession() {
                if(db && currentSession) {

                    // update local
                    // create json from surveyData and update currentSession
                    const json = JSON.stringify({
                        sessionId: currentSession.id,
                        tic: currentSession.tic,
                        items: surveyData
                    });
                    currentSession.survey = json;
                    currentSession.updatedAt = new Date();

                    // debug hack
                    if (currentSession.tic == 'debug-nofb') {
                        console.log("debug: not writing to fb");
                        console.log(JSON.stringify(currentSession));
                        return;
                    }

                    // update in fb
                    // if currentSession has no id, create a new entry
                    if (currentSession.id == '') {
                        addDoc(collection(db, fbCollectionPath), currentSession).then((ref) => {
                            console.log('added new session ' + ref.id);
                            currentSession.id = ref.id;
                        })
                    } else {
                        const docRef = doc<Session>(collection(db, fbCollectionPath), currentSession.id);
                        setDoc(docRef, currentSession).then(() => {
                            console.log('updated fb session');
                        });
                    }
                }
            };

            function evaluateSpeech(id:string, itemData: any) {

                type RunTimeData = {
                    words: string;
                    time: number;
                }
                var finalSplitted = itemData.finalSentence.split(" ")
                console.log("splitted "+finalSplitted)
                console.log("Emited:")
                console.log(itemData)

                let outputArray = []  

                let start = itemData.runtimes[0].words === "" ? itemData.runtimes[0].time : 0;


                let lastruntime = -1;
                for(let i = 0; i<finalSplitted.length; i++){
                    let currentSplit = finalSplitted.slice(0, i+1).join(" ");
                    console.log()
                    for(let j = 0; j<itemData.runtimes.length; j++){
                        //console.log("cSplit: "+currentSplit+ " | runtime: "+itemData.runtimes[j].words.toLowerCase())
                        if(currentSplit.toLowerCase() === itemData.runtimes[j].words.toLowerCase()){
                            if(j <= lastruntime){
                                //Error, transcription in wrong order.
                            }
                            else{
                                
                                outputArray.push({words:itemData.runtimes[j].words, time: itemData.runtimes[j].time - start});
                                lastruntime=j;
                                break;
                            }
                        }
                    }
                }
               
                for (let sd of surveyData) {
                        if (sd.id == currentSession.state) {
                                console.log({itemdata:itemData.finalSentence,longSentenceTotally, finalSplitted,outputArray})
                                if(itemData.finalSentence.toUpperCase().replace(" ","") === longSentenceTotally.toUpperCase().replace(" ","") && finalSplitted.length === outputArray.length) {
                                    console.log("got here");
                                    (sd as any)["runtimes"] = outputArray; 
                                    (sd as any)["finalSentence"] = itemData.finalSentence; 
                                }
                                (sd as any)["numSpoken"] += 1;
                            
                            console.log(sd)
                        }
                    }


                

                console.log("out")
                console.log(outputArray)
                sentenceAnalysis.value = outputArray;
                console.log(sentenceAnalysis.value);
                console.log("---");
                validateState();
            }

            function updateSurveyData(surveyKey:string, itemData: any) {

                console.log("updating "+surveyKey+" with ")
                console.log(itemData.value)

                if(currentSession && surveyKey != '') {
                    // get survey data object with current state id (e.g. SURVEY_DEMOGRAPHIC)
                    // add itemData to object (if it has a valid key - e.g. 'age')
                    for (let sd of surveyData) {
                        if (sd.id == currentSession.state) {
                            const key = surveyKey as keyof SurveyDataObject;
                            if (key in sd) {
