
    import { defineComponent, watch, ref} from "vue";

    export default defineComponent({
        name: 'SurveyVideo',
        emits: ['onSurveyVideo'],
        props: {
            id: String,
            videoDur: Number,
            videoFile: String,
            maxPlay: Number,
            disabled: Boolean
        },
        setup(props, context) {
            // create items based on the labels property
            // set them reactive to get view updates when they change
            const videoRef = ref<HTMLVideoElement | null>(null);
            const canvasRef = ref<HTMLCanvasElement | null>(null);
            const playbackDur = props.videoDur ? props.videoDur : 1000;
            const width = 360;
            const height = 430;
            let playing = ref(false);
            let rendering = ref(false);
            let renderBuf = ref(0);
            let playbackTime = ref(0);
            let playCount = ref(0);

            // watcher
            watch(playCount, (curValue, preValue) => {
                context.emit('onSurveyVideo', {
                    id: props.id,
                    value: curValue
                });
            });

            return {
                videoRef,
                canvasRef,
                playbackDur,
                width,
                height,
                playing,
                rendering,
                renderBuf,
                playbackTime,
                playCount
            }
        },
        mounted() {
            if (this.videoRef) {
                this.videoRef.load();
                this.videoRef.currentTime = 0;
            }
            this.startRendering();
        },
        methods: {
            play(){
                if (this.videoRef) {
                    this.videoRef.pause();
                    this.videoRef.currentTime = 0;
                    this.playbackTime = 0;
                    this.playing = true;
                    this.videoRef.play();
                }
            },
            stop() {
                this.playing = false;
                this.playbackTime = 0;
            },
            startRendering() {
                this.rendering = true;
                this.renderLoop();
            },
            renderLoop() {
                if(this.rendering) {
                    const now = Date.now();
                    const dt = now - this.renderBuf;

                    // limit render frame rate
                    if (dt > 33) {
                        this.renderBuf = now;
                        if (this.playing && this.canvasRef && this.videoRef) {
                            this.playbackTime += dt;
                            let drawVideo = true;

                            // playback complete
                            if(this.playbackTime >= this.playbackDur) {
                                this.playCount++;
                                this.stop();
                                drawVideo = false;
                            }
                            const ctx = this.canvasRef.getContext('2d');
                            if (ctx) {
                                ctx.clearRect(0, 0, this.width, this.height);
                                ctx.rect(0, 0, this.width, this.height);
                                ctx.fillStyle = '#ffffff';
                                ctx.fill();
                                if (drawVideo) {
                                    ctx.drawImage(this.videoRef, 0, 0, this.width, this.height);
                                }
                            }
                        }
                    }

                    requestAnimationFrame(this.renderLoop);
                }
            }
        }
    })
