
    import { defineComponent, onMounted, ref, watch} from "vue";

    export default defineComponent({
        name: 'SurveyInput',
        emits: ['onSurveyInput'],
        props: {
            id: String,
            question: String,
            inputType: String,
            placeholder: String,
            mandatory: Boolean,
            disabled: Boolean
        },
        setup(props, context) {
            // define data and watchers
            const value = ref('');
            watch(value, (curValue, preValue) => {
                context.emit('onSurveyInput', {
                    id: props.id,
                    value: curValue
                });
            });

            onMounted(() => {
                console.log("mounted");
            })

            // return data
            return {
                value
            }
        }
    })
